import React from 'react';
import styled from 'styled-components';

interface AvatarProps {
  imagePath: string;
}

const AvatarContainer = styled.div<AvatarProps>`
  border-radius: 2.5rem;
  width: 2.5rem;
  height: 2.5rem;
  flex-shrink: 0;
  background: ${({imagePath}) => `url(/assets/icons/Oval.svg),  lightgray 50% / cover no-repeat`};
`;

const Avatar: React.FC<AvatarProps> = ({ imagePath }) => {
    const src = "/assets/icons/Oval.svg"

  return <AvatarContainer imagePath={src} />;
};

export default Avatar;

